<template>
        <v-container  style="color: #A5BBC9 !important;">
            <v-row dense>
                <v-col cols="12">
                    <v-card flat color="#1A1E24">
                        <v-card-text>
                            <v-row dense>
                                <v-col cols="11">
                                    <v-row dense>
                                        <v-col cols="11">
                                            <v-text-field v-model="header" dense regular label="Заголовок" />
                                        </v-col>
                                        <v-col cols="1">
<!--                                            <speech-file-input type-input="mainInput"-->
<!--                                                               :text.sync="header"-->
<!--                                                               title="Заголовок"></speech-file-input>-->
                                        </v-col>
                                    </v-row>
                                </v-col>
                                <v-col cols="11">
                                    <v-row dense>
                                        <v-col cols="11">
                                            <v-textarea outlined v-model="note" dense label="Описание задачи" />
                                        </v-col>
                                        <v-col cols="1">
                                            <speech-file-input type-input="textarea"
                                                               :text.sync="note"
                                                               title="Описание задачи" />
                                        </v-col>
                                    </v-row>
                                </v-col>
                                <v-col cols="5">
                                    <v-combobox
                                            v-model="taskPriority"
                                            :items="priority"
                                            item-text="name"
                                            item-value="id"
                                            label="Приоритет"
                                            small-chips
                                            outlined
                                            dense
                                    >
                                        <template #selection="{ item }">
                                            <v-chip small :color="item.color">{{item.name}}</v-chip>
                                        </template>
                                    </v-combobox>
                                </v-col>
                                <v-col cols="5" >
                                    <v-select label="Вид поручения"
                                              v-model="taskTypeSelect"
                                              :items="taskType"
                                              item-text="value"
                                              item-value="id"
                                              small-chips
                                              dense
                                              item-color="#FDCA40"
                                              outlined>
                                        <template #selection="{ item }">
                                            <v-chip small color="#FDCA40">{{item.value}}</v-chip>
                                        </template>
                                    </v-select>
                                </v-col>
                                <v-col cols="5">
                                    <v-text-field dense
                                                  label="Автор задачи"
                                                  outlined
                                                  :disabled="!editMode"
                                    />
                                </v-col>
                                <v-col cols="5">
                                    <v-dialog
                                            ref="dialog"
                                            v-model="modal"
                                            :return-value.sync="date"
                                            persistent
                                            width="340"
                                    >
                                        <template v-slot:activator="{ on }">
                                            <v-text-field
                                                    v-model="validUntil"
                                                    label="Срок исполнения"
                                                    readonly
                                                    dense
                                                    outlined
                                                    v-on="on"
                                            ></v-text-field>
                                        </template>
                                        <v-date-picker  v-model="validUntil" first-day-of-week="1" >
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="modal = false">Закрыть</v-btn>
                                            <v-btn text color="primary" @click="$refs.dialog.save(validUntil)">Сохранить</v-btn>
                                        </v-date-picker>
                                    </v-dialog>
                                </v-col>
                                <v-col cols="5">
                                    <treeselect v-model="ispolnitel"
                                                :options="userTree"
                                                :multiple="true"
                                                class="vue-treeselect__control"
                                                placeholder="Ответственный"
                                                style=" border: 1px solid #515458;"
                                    />
                                </v-col>
                                <v-col cols="5">
                                    <v-file-input dense placeholder="Документ" outlined/>
                                </v-col>
                                <v-col cols="4">
                                    <v-checkbox dense
                                                v-model="allowResponses"
                                              label="Выключить комментарии"
                                    ></v-checkbox>
                                </v-col>
                            </v-row>
                            <v-subheader>
                                Дополнительно
                            </v-subheader>
                            <v-row dense v-if="editMode">
                                <v-col cols="10">
                                    <v-select :items="taskState"
                                              item-value="id"
                                              item-text="value"
                                              v-model="taskStateSelect"
                                              label="Статус задачи" />
                                </v-col>
                                <v-col cols="10">
                                    <v-text-field   dense
                                                    v-model="reaction"
                                                 label="Реакция"
                                                 outlined
                                    />
                                    <v-row dense class="text-left">
                                        <v-subheader>
                                            Быстрые ответы
                                        </v-subheader>
                                        <v-col cols="9">
                                            <v-chip
                                                    small
                                                    class="ma-2"
                                                    @click="reaction=`Принято!`"
                                            >
                                                Принято!
                                            </v-chip>
                                            <v-chip
                                                    small
                                                    class="ma-2"
                                                    @click="reaction=`Передано в работу!`"
                                            >
                                                Передано в работу цеха!
                                            </v-chip>
                                            <v-chip
                                                    small
                                                    class="ma-2"
                                                    @click="reaction=`Изучаю`"
                                            >
                                                Изучаю
                                            </v-chip>
                                            <v-chip
                                                    small
                                                    class="ma-2"
                                                    @click="reaction=`В работе!`"
                                            >
                                                В работе!
                                            </v-chip>
                                        </v-col>
                                    </v-row>
                                </v-col>
                                <v-col cols="11">
                                    <v-row dense>
                                        <v-col cols="11">
                                            <v-textarea v-model="comment" outlined placeholder="Комментарий к задаче" />
                                        </v-col>
                                        <v-col cols="1">
                                            <speech-file-input type-input="textarea"
                                                               :text.sync="comment"
                                                               title="Комментарий" />
                                        </v-col>
                                    </v-row>

                                    <v-btn @click="addCommentTask()">
                                        Добавить комментарий
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn width="200" color="green lighteen-1"
                                    to="/tasks">
                                На главную
                            </v-btn>
                            <v-btn width="200" style="background-color: #E9F7FB; color: #27B4E0;"
                                   to="/tasks">
                                Отменить
                            </v-btn>
                            <v-btn @click="createTask" width="200" style="background: linear-gradient(360deg, #0496FF 0%, #28B5E0 100%);">
                                Сохранить
                            </v-btn>
                            <v-btn color="error" @click="deleteTask(idTask)">
                                Удалить запись
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
</template>

<script>
    import Treeselect from '@riophae/vue-treeselect'
    // import the styles
    import '@riophae/vue-treeselect/dist/vue-treeselect.css'
    import SpeechFileInput from "./SpeechFileInput";
    import {mapActions, mapState} from 'vuex'
    import APiServices from "../../services/APiServices";
    export default {
        name: "CreateTask",
        components: {SpeechFileInput, Treeselect},
        data: () => ({
            taskStateSelect: -1,
            taskPriority: '',
            header: '',
            taskTypeSelect: '',
            validUntil: '',
            comment: '',
            reaction: '',
            inspolnitel: '',
            allowResponses: false,
            note: '',
            priority: [
                {
                    id: 1,
                    color: '#FF715B',
                    name: 'Высокий'
                },
                {
                    id: 2,
                    color: '#28B5E0',
                    name: 'Средний'
                },
                {
                    id: 3,
                    color: '#30D222',
                    name: 'Низкий'
                },
            ]
        }),
        computed:{
            ...mapState('tasks', {
                userTree: 'userTree',
                taskType: 'taskType',
                taskState: 'taskState'
            }),
            editMode(){
                return  Boolean(this.$root.$route.params.idtask)
            },
            idTask(){
                return  this.$root.$route.params.idtask
            }
        },
        methods: {
            speechEndHeader ({sentences, header}) {
                // console.log('text', header)
                // console.log('sentences', sentences)
                // //this.sentences = sentences
            },
            speechEndDescription ({sentences, description}) {
                // console.log('text', description)
                // console.log('sentences', sentences)
                // //this.sentences = sentences
            },
            ...mapActions('tasks', {
                getUserTree: 'get_user_tree',
                getTaskType: 'get_task_type',
                addTask: 'add_task',
                getTaskState: 'get_task_state',
                updateTask: 'update_task',
                addComment: 'add_comment_task',
                getCommentsTask: 'get_comments_task'
            }),
            async addCommentTask(){
                let id = this.$root.$route.params.idtask
                await this.addComment({
                    comment: this.comment,
                    idTask: id
                })
                this.getCommentsTask(id)

            },
            async createTask(){
                if (this.$root.$route.params.idtask) {
                    this.updateTask({
                        id: this.$root.$route.params.idtask,
                        forUserIds: this.ispolnitel,
                        validUntil: this.validUntil,
                        header: this.header,
                        priority: this.taskPriority.id,
                        reaction: this.reaction,
                        note: this.note,
                        file: '',
                        color: '',
                        isCommentingAllowed: !this.isCommentingAllowed,
                        cardType: this.taskTypeSelect,
                        cardState: this.taskStateSelect,
                        allowResponses: this.allowResponses
                    })
                }else{
                    this.addTask({
                        forUserIds: this.ispolnitel,
                        validUntil: this.validUntil,
                        header: this.header,
                        priority: this.taskPriority.id,
                        reaction: this.reaction,
                        note: this.note,
                        file: '',
                        color: '',
                        isCommentingAllowed: !this.isCommentingAllowed,
                        cardType: this.taskTypeSelect,
                        cardState: this.taskStateSelect,
                        allowResponses: this.allowResponses
                    })
                    this.$router.push({path: '/tasks'})
                }

            },
            async deleteTask(idTask){
                  await APiServices.deleteTask(idTask)
                this.$root.$router.push({path: '/tasks'})
            }
        },
        async created() {
            this.getUserTree()
            this.getTaskType()
            this.getTaskState()
            if (this.$root.$route.params.idtask){
                let editTask = await APiServices.getTask(this.$root.$route.params.idtask)
                this.validUntil = editTask.data.value.validUntil
                this.ispolnitel = editTask.data.value.forUserIds
                this.header = editTask.data.value.header
                this.note = editTask.data.value.note
                this.taskPriority = this.priority.find(x=>x.id==editTask.data.value.priority)
                this.taskTypeSelect = editTask.data.value.cardType
            }
        }
    }
</script>

<style>
.vue-treeselect__control{
  border: none;
  background: #1a1e24;
}
.vue-treeselect__menu {
  color: white !important;
  background: #1a1e24 !important;
}
.vue-treeselect__value-container{
  background: #1a1e24;
}
.vue-treeselect__menu:hover{
  color: black !important;
  background: #1a1e24 !important;
}

</style>
