import axios from '../plugins/axios'

async function getUserTree() {
    return  await axios.get('/api/v1/usertree')
}


async function getTasks() {
    return await axios.get('/api/v1/card')
}
async function getTask(id) {
    return  await axios.get('/api/v1/card/'+id)
}

async function addTask(task) {
    return await axios.post('/api/v1/card', {
        forUserIds: task.forUserIds,
        validUntil: task.validUntil,
        header: task.header,
        priority: task.priority,
        reaction: task.reaction,
        note: task.note,
        file: task.file,
        color: task.color,
        isCommentingAllowed: task.isCommentingAllowed,
        cardType: task.cardType,
        cardState: task.cardState
    })
}
async function updateTask(task){
    return await axios.post('/api/v1/card/'+task.id,{
        forUserIds: task.forUserIds,
        validUntil: task.validUntil,
        header: task.header,
        priority: task.priority,
        reaction: task.reaction,
        note: task.note,
        file: task.file,
        color: task.color,
        isCommentingAllowed: task.isCommentingAllowed,
        cardType: task.cardType,
        cardState: task.cardState,
        allowResponses: task.allowResponses
    })
}
async function deleteTask(idTask){
    return await axios.delete('/api/v1/card/'+idTask)
}
async  function getCommentsTask(taskId){
    return  await axios.get(`/api/v1/cardmessage/${taskId}`)
}
async function addCommentTask(comment) {
    return await axios.post('/api/v1/cardmessage/'+comment.idTask, {
        message: comment.comment,
        typeId: 0
    })
}

async function getCardTypes() {
    return await axios.get('/api/v1/enums/cardtypes')
}
async function getCardStateTypes() {
    return await axios.get('/api/v1/enums/cardstatetypes')
}
async function getCardCommentTypes() {
    return await axios.get('/api/v1/enums/cardcommenttypes')
}

async function getHistoryTask(taskId) {
    return await axios.get('/api/v1/card/history/'+taskId)
}
async  function addStickTask(idTask) {
    return await axios.post('/api/v1/card/stick/'+idTask)
}
async function deleteStickTask(idTask) {
    return await axios.delete('/api/v1/card/stick/'+idTask)
}
export default {
    getUserTree,
    getTasks,
    addTask,
    getCommentsTask,
    addCommentTask,
    getCardCommentTypes,
    getCardStateTypes,
    getCardTypes,
    getTask,
    getHistoryTask,
    updateTask,
    deleteTask,
    addStickTask,
    deleteStickTask
}
